div#carbox{
  width:300px;
  border: none;
  border-radius: 35px;
  margin-top: 40px;
  margin-left: 20px;
  height:300px;
  float: left;
  background-color: rgba(255, 255, 255, 0.863);
}

div#carbox:first-child{
  margin-left: 0;
}

div#carbox img{
  border: 1px black solid;
  width: 94%;
  margin-left: 3%;
  height: 150px;
  display: block;
  margin-top: 20px;
}

div#carbox #info a, h2{
  width: 90%;
  text-align: center;
  margin-left: 5%;

}

div#carbox #info{
  display: block;
  width: 100%;
  text-align: center;
}

div#carbox a{
  display: block;
  width: 30%;
  height: 35px;
  font-weight: bold;
  font-size: 14px;
  margin-left: 30%;
  margin-top: 20px;
  text-decoration: none;
}

h1#carlistheader{
  text-align: center;
}

#dane {
  width: 300px;
  height: 300px;
  padding: 0px;
  border: 2px solid black;
  position: absolute;
  left: 58%;
  top: 20%;
  text-align: left;
}

#Dane_wypozyczenia
{
  position: absolute;
  text-decoration: none;
  padding: 0px;
  top: 50%; 
  left: 50%;
  text-align: center;
